import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
const routerPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error => error)
}

const router = new Router({
  routes: [
    {
      path: "/",
      component: () => import(/* webpackChunkName: "Login" */ '../views/Login.vue'),
      meta: { title: '用户登入' },
    },
    {
      path: '/home',
      component: () => import(/* webpackChunkName: "home" */ '../common/Home.vue'),
      meta: { title: '拓客汇', auth: true },
      children: [
        {
          path: "/index",
          component: () => import(/* webpackChunkName: "index" */ '../views/Index.vue'),
          meta: { title: '系统首页', auth: true }
        },
        {
          path: "/act/activity",
          component: () => import(/* webpackChunkName: "act" */ '../views/Act.vue'),
          meta: { title: '活动管理', auth: true },
          children: [
            {
              path: "/act/activity",
              component: () => import(/* webpackChunkName: "activity" */ '../components/act/Activity.vue'),
              meta: { title: '活动列表', auth: true },
            },
            {
              path: "/act/message",
              component: () => import(/* webpackChunkName: "message" */ '../components/act/Message.vue'),
              meta: { title: '评论管理', auth: true },
            },
            {
              path: "/act/message/change",
              component: () => import(/* webpackChunkName: "ChangeMsg" */ '../components/act/ChangeMsg.vue'),
              meta: { title: '修改评论', auth: true },
            },
            {
              path: "/act/reserve",
              component: () => import(/* webpackChunkName: "reserve" */ '../components/act/Reserve.vue'),
              meta: { title: '预约信息', auth: true },
            },
            {
              path: "/act/card",
              component: () => import(/* webpackChunkName: "Card" */ '../components/act/Card.vue'),
              meta: { title: '卡劵列表', auth: true },
            },
            {
              path: "/act/activity/addcard",
              component: () => import(/* webpackChunkName: "Addcard" */ '../components/act/Addcard.vue'),
              meta: { title: '添加卡劵', auth: true },
            },
            {
              path: "/act/activity/editorcard",
              component: () => import(/* webpackChunkName: "EditorCard" */ '../components/act/EditorCard.vue'),
              meta: { title: '编辑卡劵', auth: true },
            },
            {
              path: "/act/activity/create",
              component: () => import(/* webpackChunkName: "Createact" */ '../components/act/act-create/Createact.vue'),
              meta: { title: '创建活动', auth: true },
            },
            {
              path: "/act/activity/editor",
              component: () => import(/* webpackChunkName: "Editor" */ '../components/act/Editor.vue'),
              meta: { title: '活动编辑', auth: true },
            }
          ]
        },
        {
          path: "/log/upload",
          component: () => import(/* webpackChunkName: "log" */ '../views/Log.vue'),
          meta: { title: '文件管理', auth: true },
          children: [
            {
              path: "/log/upload",
              component: () => import(/* webpackChunkName: "upload" */ '../components/log/Upload.vue'),
              meta: { title: '上传文件', auth: true },
            }
          ]
        },
        {
          path: "/sys/tip",
          component: () => import(/* webpackChunkName: "sys" */ '../views/Sys.vue'),
          meta: { title: '系统公告', auth: true },
          children: [
            {
              path: "/sys/tip",
              component: () => import(/* webpackChunkName: "Tip" */ '../components/sys/Tip.vue'),
              meta: { title: '公告列表', auth: true },
            },
            {
              path: "/sys/tip/detail",
              component: () => import(/* webpackChunkName: "Detail" */ '../components/sys/Detail.vue'),
              meta: { title: '公告详情', auth: true },
            },
            {
              path: "/sys/tip/release",
              component: () => import(/* webpackChunkName: "release" */ '../components/sys/Release.vue'),
              meta: { title: '发布公告', auth: true },
            },
          ]
        },
        {
          path: "/funds/cash",
          component: () => import(/* webpackChunkName: "Funds" */ '../views/Funds.vue'),
          meta: { title: '资金管理', auth: true },
          children: [
            {
              path: "/funds/cash",
              component: () => import(/* webpackChunkName: "Cash" */ '../components/cash/Cash.vue'),
              meta: { title: '提现管理', auth: true },
            },
            {
              path: "/funds/cashuser",
              component: () => import(/* webpackChunkName: "Cashuser" */ '../components/cash/Cashuser.vue'),
              meta: { title: '用户提现日志', auth: true },
            },
            {
              path: "/funds/payment",
              component: () => import(/* webpackChunkName: "Payment" */ '../components/cash/Payment.vue'),
              meta: { title: '充值管理', auth: true },
            },
            {
              path: "/funds/payment/del",
              component: () => import(/* webpackChunkName: "PayMentdel" */ '../components/cash/PayMentdel.vue'),
              meta: { title: '充值详情', auth: true },
            },
            {
              path: "/funds/cash/opencash",
              component: () => import(/* webpackChunkName: "Opencash" */ '../components/cash/Opencash.vue'),
              meta: { title: '提现申请', auth: true },
            },
            {
              path: "/funds/payment/create",
              component: () => import(/* webpackChunkName: "payment" */ '../components/cash/Create.vue'),
              meta: { title: '充值', auth: true },
            }
          ]
        },
        {
          path: "/teacher",
          component: () => import(/* webpackChunkName: "teacher" */ '../views/Teacher.vue'),
          meta: { title: '获客大师', auth: true },
        },
        {
          path: "/user/info",
          component: () => import(/* webpackChunkName: "user" */ '../views/User.vue'),
          meta: { title: '个人中心', auth: true },
          children: [
            {
              path: "/user/info",
              component: () => import(/* webpackChunkName: "info" */ '../components/user/Info.vue'),
              meta: { title: '个人信息', auth: true },
            },
            {
              path: "/user/password",
              component: () => import(/* webpackChunkName: "password" */ '../components/user/Password.vue'),
              meta: { title: '修改密码', auth: true },
            },
            {
              path: "/user/wx",
              component: () => import(/* webpackChunkName: "wx" */ '../components/user/Wx.vue'),
              meta: { title: '微信绑定', auth: true },
            },
            {
              path: "/user/appmsg",
              component: () => import(/* webpackChunkName: "Set" */ '../components/user/appMsg.vue'),
              meta: { title: '应用信息', auth: true },
            },
            {
              path: "/user/addAppMsg",
              component: () => import(/* webpackChunkName: "addAppMsg" */ '../components/user/addAppMsg.vue'),
              meta: { title: '添加应用信息', auth: true },
            },
            {
              path: "/user/changeAppMsg",
              component: () => import(/* webpackChunkName: "changeAppMsg" */ '../components/user/changeAppMsg.vue'),
              meta: { title: '修改应用消息', auth: true },
            },
          ]
        },
        {
          path: "/users",
          component: () => import(/* webpackChunkName: "users" */ '../views/Users.vue'),
          meta: { title: '用户管理', auth: true },
          children: [
            {
              path: "/users/list",
              component: () => import(/* webpackChunkName: "password" */ '../components/users/Userlist.vue'),
              meta: { title: '用户列表', auth: true },
            },
            {
              path: "/users/add",
              component: () => import(/* webpackChunkName: "password" */ '../components/users/Adduser.vue'),
              meta: { title: '用户添加', auth: true },
            },
            {
              path: "/users/up",
              component: () => import(/* webpackChunkName: "password" */ '../components/users/Updata.vue'),
              meta: { title: '用户更新', auth: true },
            },
          ]
        },
        {
          path: "/pms",
          component: () => import(/* webpackChunkName: "Pms" */ '../views/Pms.vue'),
          meta: { title: '权限管理', auth: true },
          children: [
            {
              path: "/pms/poal",
              component: () => import(/* webpackChunkName: "Poal" */ '../components/pms/Poal.vue'),
              meta: { title: '权限列表', auth: true },
            },
            {
              path: "/pms/poaldel",
              component: () => import(/* webpackChunkName: "poalDel" */ '../components/pms/poalDel.vue'),
              meta: { title: '权限详情', auth: true },
            }
          ]
        },
        {
          path: "/demo",
          component: () => import(/* webpackChunkName: "Pms" */ '../views/demo.vue'),
          meta: { title: '权限管理', auth: true },
        }
      ]
    }
  ]
})
// 导航守卫
router.beforeEach((to, from, next) => {
  var uid = localStorage.getItem("uid");
  if (to.meta.auth) {
    if (uid) {
      next()
    } else {
      next({
        path: "/"
      })
    }
  } else {
    if (from.path !== "/" && to.path == "/") {
      next({
        path: "/index"
      })
    } else {
      next()
    }
  }
})
export default router;



